<template>
  <div class="loading">
    <!-- <van-overlay :show="show" @click="show = false" /> -->
    <h2>{{ loadingBox }}</h2>
  </div>
</template>

<script>
import Vue from 'vue'
import { Toast, Overlay } from 'vant'
Vue.use(Overlay).use(Toast)
export default {
  name: 'Loading',
  data () {
    return {
      msg: 'Welcome To Loading',
      show: true
    }
  },
  methods: {},
  components: {},
  computed: {
    loadingBox () {
      Toast.loading({
        message: '加载中...',
        forbidClick: true,
        loadingType: 'spinner'
      })
    }
  }
}
</script>

<style lang="less" scoped>
.loading {
  width: 100%;
  height: 100%;
}
</style>
