<template>
  <div class="account">
    <keep-alive>
      <nav class="nav-bar">
        <ul class="nav-ul">
          <li
            v-for="item in navbar"
            :class="{isActive:item.status}"
            :key="item.id"
            @click="navTab(item)"
          >
            <router-link :to="item.link">{{ item.name }}</router-link>
          </li>
        </ul>
      </nav>
    </keep-alive>
    <section class="main-body">
      <router-view></router-view>
    </section>
  </div>
</template>

<script>
export default {
  name: "Account",
  data() {
    return {
      msg: "Welcome Account",
      navbar: [
        { id: 0, name: "购物车", link: "/account/cart", status: true },
        { id: 1, name: "我的订单", link: "/account/order", status: false },
        { id: 2, name: "我发布的问题", link: "/account/qa", status: false },
        { id: 3, name: "我的地址", link: "/account/address", status: false }
      ]
    };
  },
  created() {
    this.setPathActive(location.hash.replace(/#/, ""));
  },
  methods: {
    navTab(e) {
      this.navbar.forEach(item => (item.status = false));
      this.navbar[e.id].status = true;
    },
    setPathActive(e) {
      this.navbar.forEach(item => {
        item.status = false;
        if (item.link === e) {
          item.status = true;
        }
      });
    }
  },
  components: {},
  computed: {}
};
</script>

<style lang="less" scoped>
@fff: #fff;
@eee: #eee;
@ff3: #ff3900;
@ff6: #ff6c00;
@f7f: #f7f7f7;
@333: #333;
@143: #143365;
.account {
  .wh() {
    width: 100%;
    height: 100%;
  }
  background-color: @eee;
  padding-bottom: 90px;
  .nav-bar {
    width: 90%;
    margin: 0 auto;
    box-sizing: border-box;
    padding-top: 80px;
    height: 220px;
    .nav-ul {
      display: flex;
      justify-content: flex-start;
      li {
        a {
          display: block;
          padding: 0 32px;
          font-size: 36px;
          font-weight: 600;
          color: inherit;
        }
      }
      .isActive {
        color: @ff6;
      }
    }
  }
  .main-body {
    background-color: @fff;
    width: 90%;
    margin: 0 auto;
    box-sizing: border-box;
    padding: 30px 0;
    min-height: 350px;
  }
}
</style>
