<template>
  <div class="wrapper" id="app">
    <keep-alive>
      <Header />
    </keep-alive>
    <main class="main">
      <router-view />
    </main>
    <Footer />
    <Loading v-if="loading" />
  </div>
</template>

<script>
import { mapState } from 'vuex'
import Header from '@/components/comm/Header'
import Footer from '@/components/comm/Footer'
import Loading from '@/components/comm/Loading'
import jwt_decode from 'jwt-decode'
export default {
  name: 'App',
  data () {
    return {
      msg: 'kupl'
    }
  },
  created () {
    if (localStorage.jwtToken) {
      const decode = jwt_decode(localStorage.jwtToken)
      console.log(decode)
      if (!this.$utils.isEmpty(decode)) {
        this.$store.dispatch('login', decode.memberUser)
      } else {
        this.$store.dispatch('loginout')
      }
    }
  },
  components: {
    Header,
    Footer,
    Loading
  },
  computed: {
    ...mapState(['loading'])
  }
}
</script>

<style lang="less">
@bgc: #f7f7f7;
html,
body {
  width: 100%;
  height: 100%;
  padding: 0;
  margin: 0;
}
html {
  overflow: hidden;
  font-size: 64px; /*no*/
  min-width: 320px; /*no*/
}
body {
  overflow: auto;
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #333;
}

// 全局样式
.wrapper {
  width: 100%;
  min-height: 100%;
  position: relative;
}
.main {
  //无友情链接820px
  padding-bottom: 1340px;
}
.footer {
  position: absolute;
  width: 100%;
  bottom: 0;
  background-color: @bgc;
}
</style>
