<template>
  <div class="searchbox">
    <div class="s-item">
      <input type="text" v-model="value" placeholder="搜索商品名称、编号、条码" />
      <button type="button" @click="serachBtn"></button>
    </div>
  </div>
</template>

<script>
import { Toast } from "vant";
export default {
  name: "SearchBox",
  props: {
    keyword: {
      type: String,
      default: ""
    }
  },
  data() {
    return {
      value: ""
    };
  },
  created() {
    this.value = this.keyword;
    if (location.hash.replace(/#/, "") == "/search") {
      this.searchList();
    }
  },
  methods: {
    serachBtn() {
      let that = this;
      let kw = this.value;
      if (this.$utils.isEmpty(kw)) {
        Toast.fail("检索内容不能为空！");
      } else {
        that.searchList();
        that.$router.push({
          path: "/search",
          query: { kw, tm: Date.now() }
        });

        // 加载弹框
        // let to = Toast.loading({
        //   message: "加载中...",
        //   forbidClick: true
        // });
        // let timer = setTimeout(() => {
        //   clearTimeout(timer);
        //   to.clear();
        //   // 页面跳转传参
        //   that.$router.push({
        //     path: "/search",
        //     query: { searchKey, times: new Date().getTime() }
        //   });
        // }, 800);
      }
    },
    searchList() {
      let that = this;
      let params = setParams({});

      console.log("检索参数:", params);

      // 数据请求
      this.$fetch("/api/Product/GetProducts", params).then(res => {
        console.log(res);
      });

      // 构造参数
      function setParams(obj) {
        return {
          keyword: that.value,
          orderBy: obj.orderBy || "id",
          order: obj.order || "desc",
          page: obj.page || 1,
          size: obj.size || 10
        };
      }
    }
  },
  components: {},
  computed: {}
};
</script>

<style lang="less" scoped>
.searchbox {
  height: 190px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  .s-item {
    box-sizing: border-box;
    width: 718px;
    height: 68px;
    background: url("./../../assets/comm/search.png") no-repeat left top;
    background-size: 100% 100%;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    input,
    button {
      border: none;
      height: 66px;
      background-color: transparent;
    }
    input {
      width: 486px;
      font-size: 18px;
    }
    button {
      width: 152px;
      border-radius: 16px;
    }
  }
}
</style>
