<template>
  <header class="header">
    <ul class="top-bar">
      <li class="user">
        <!-- <router-link to="/login" v-show="!hasLogin">
          <img src="./../../assets/header/user.png" />
        </router-link>-->
        <router-link to="/login" v-show="!hasLogin">
          <van-icon name="user-circle-o" color="#143365" size="1.8em" />
        </router-link>
        <router-link to="/account" v-show="hasLogin">
          <van-icon name="user-circle-o" :info="userName" color="#143365" size="1.8em" />
        </router-link>
      </li>
      <li class="logo">
        <router-link to="/home">
          <img src="./../../assets/header/logo.png" />
        </router-link>
      </li>
      <li class="menu" @click="showPopup">
        <a href="javascript:;">
          <img src="./../../assets/header/menu.png" />
        </a>
      </li>
    </ul>
    <nav class="header-nav">
      <van-popup v-model="show" position="right" :style="{ height: '100%',width:'45%' }">
        <div class="nav-tab">
          <ul class="nav-ul">
            <li
              v-for="(item,index) in navbars"
              :key="index"
              :class="{'nav-active':item.isActive}"
              @click="navTabBtn(item.link)"
            >
              <router-link :to="item.link">
                <van-icon :name="item.icon" />
                <strong>{{ item.name }}</strong>
              </router-link>
            </li>
          </ul>
        </div>
        <div class="user-tab">
          <ul class="user-ul" v-show="!hasLogin">
            <li @click="userBtn">
              <router-link to="/login">
                <van-icon name="user-o" />
                <strong>登录</strong>
              </router-link>
            </li>
            <li @click="userBtn">
              <router-link to="/register">
                <van-icon name="passed" />
                <strong>注册</strong>
              </router-link>
            </li>
          </ul>
          <div class="login-out" v-show="hasLogin">
            <van-button icon="paid" type="warning" @click="account('/account')">会员中心</van-button>
            <van-button icon="stop-circle-o" type="danger" @click="loginout">用户注销</van-button>
          </div>
        </div>
      </van-popup>
    </nav>
  </header>
</template>

<script>
import Vue from "vue";
import { mapState } from "vuex";
import { Popup, Sidebar, SidebarItem, Icon, Button, Dialog } from "vant";

Vue.use(Popup)
  .use(Sidebar)
  .use(Icon)
  .use(Dialog)
  .use(Button)
  .use(SidebarItem);
export default {
  name: "Header",
  data() {
    return {
      show: false,
      activeKey: 0
    };
  },
  created(e) {
    console.log(location.hash);
    this.$store.dispatch("setNavActvie", location.hash.replace(/#/, ""));
  },
  methods: {
    showPopup() {
      this.show = true;
    },
    navTabBtn(e) {
      // this.navbars.forEach(item => (item.isActive = false));
      // this.navbars[e].isActive = true;
      this.$store.dispatch("setNavActvie", e);
      this.show = false;
    },
    userBtn() {
      this.show = false;
    },
    account(e) {
      this.show = false;
      if (location.hash.replace(/#/, "") != e) {
        this.$router.push(e);
      }
    },
    loginout() {
      Dialog.confirm({
        title: "温馨提示",
        message: "您确认要注销当前账户吗？"
      })
        .then(() => {
          // on confirm
          this.show = false;
          this.$store.dispatch("loginout");
          this.$router.push({ path: "/login" });
        })
        .catch(() => {
          // on cancel
        });
    }
  },
  computed: {
    ...mapState(["navbars", "hasLogin", "userName"])
  }
};
</script>

<style lang="less" scoped>
@bgdc: #fe8c00;
@acitve: #143365;
@fff: #fff;
.header {
  width: 100%;
  height: 260px;
  background-color: @fff;
  .top-bar {
    display: flex;
    height: 100%;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    .user {
      a {
        img {
          width: 45px;
          height: 48px;
        }
      }
    }
    .logo {
      a {
        img {
          width: 195px;
          height: 84px;
        }
      }
    }
    .menu {
      a {
        img {
          width: 50px;
          height: 40px;
        }
      }
    }
  }

  .header-nav {
    position: relative;
    .nav-tab,
    .user-tab {
      ul {
        li {
          a {
            display: block;
            height: 120px;
            line-height: 120px;
            color: inherit;
            i,
            strong {
              color: inherit;
              display: inline-block;
              vertical-align: middle;
              font-size: 40px;
              margin-right: 10px;
            }
          }
        }
      }
    }
    .nav-tab {
      ul {
        padding: 90px 0 0 50px;
        .nav-active {
          color: @acitve;
          font-weight: 600;
        }
      }
    }
    .user-tab {
      border-top: 1px solid #eee; /*no*/
      width: 100%;
      position: absolute;
      bottom: 8%;
      ul {
        padding: 20px 0 0 50px;
      }
      .login-out {
        padding-top: 50px;
        text-align: center;
        button {
          width: 80%;
          height: 100px;
          line-height: 100px;
          background-color: @acitve;
          border: 1px solid @acitve;
        }

        .van-button--warning {
          background-color: @bgdc;
          border: 1px solid @bgdc;
          margin-bottom: 18px;
        }
      }
    }
  }
}
</style>
