<template>
  <div class="register">
    <TopHeader>
      <template v-slot:title>用户注册</template>
    </TopHeader>
    <div class="register-text">
      <van-cell-group>
        <van-field
          v-model="username"
          required
          clearable
          label="用户名"
          right-icon="question-o"
          placeholder="请输入用户名"
          @click-right-icon="$toast('请输入用户名')"
          error
        />
        <van-field v-model="phone" clearable label="手机号" placeholder="请输入手机号" required error />
        <van-field v-model="email" clearable label="邮箱" placeholder="请输入邮箱" required error />
        <van-field
          v-model="password"
          clearable
          type="password"
          label="密码"
          placeholder="请输入密码"
          required
          error
        />
        <van-field
          v-model="password2"
          clearable
          type="password"
          label="确认密码"
          placeholder="请输确认密码"
          required
          error
        />
      </van-cell-group>
      <div class="register-info">
        <van-checkbox v-model="checkedUser" shape="square" checked-color="#fe643d">
          <strong>我已看过并接受</strong>
          <a href="javascript:;">
            &#12298;
            <span>用户协议</span>
            &#12299;
          </a>
        </van-checkbox>
      </div>
    </div>
    <div class="register-btn">
      <van-button type="danger" @click="register">立即注册</van-button>
    </div>
    <div class="register-code">
      <span>已有账号！</span>
      <router-link to="/login">
        <strong>马上登录</strong>
      </router-link>
    </div>
  </div>
</template>

<script>
import Vue from 'vue'
import TopHeader from '@/views/comm/TopHeader'
import { Field, Button, Cell, CellGroup, Checkbox, CheckboxGroup } from 'vant'
Vue.use(Field)
  .use(Button)
  .use(Cell)
  .use(Checkbox)
  .use(CheckboxGroup)
  .use(CellGroup)
export default {
  name: 'Register',
  data () {
    return {
      checkedUser: true,
      username: '',
      phone: '',
      email: '',
      password2: '',
      password: ''
    }
  },
  methods: {
    register () {
      let that = this
      let users = {
        userName: this.username,
        password: this.password,
        email: this.email,
        mobile: this.phone
      }

      if (this.$utils.isEmpty(users.userName)) {
        return this.$toast.fail('用不名能为空！')
      }
      if (this.$utils.isPhone(users.phone)) {
        return this.$toast.fail('手机号格式不正确！')
      }
      if (!this.$utils.isEmail(users.email)) {
        return this.$toast.fail('邮箱格式不正确！')
      }
      if (this.$utils.isEmpty(users.password)) {
        return this.$toast.fail('密码不能为空！')
      }
      if (!this.$utils.isEquals(users.password, this.password2)) {
        return this.$toast.fail('两次密码不一致！')
      }

      // 登录请求
      that.$post('/api/Member/Add', users).then(res => {
        if (!res.code) {
          that.$toast.success('注册成功！')
          that.$router.push('/login')
        } else {
          return that.$toast.fail('注册失败！')
        }
      })
    }
  },
  components: {
    TopHeader
  },
  computed: {}
}
</script>

<style lang="less" scoped>
@fc8: #fc8481;
@fe6: #fe643d;
.register {
  padding-bottom: 288px;
  .register-text {
    padding: 104px 0 0;
    width: 816px;
    margin: 0 auto;
    .register-info {
      padding: 94px 0 104px;
      margin: 0 auto;
      .van-checkbox__icon--checked {
        .van-icon-success {
          background-color: @fc8;
          border-color: @fc8;
        }
      }
      strong,
      a {
        display: inline-block;
        vertical-align: middle;
      }
      a {
        color: @fc8;
      }
    }
  }
  .register-btn {
    width: 816px;
    margin: 0 auto;
    text-align: center;
    button {
      width: 596px;
      background-color: @fe6;
      border: 1px solid @fe6;
    }
  }
  .register-code {
    width: 816px;
    margin: 0 auto;
    text-align: right;
    padding-top: 90px;
    a {
      color: @fe6;
    }
  }
}
</style>
