import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const store = new Vuex.Store({
  state: { // 是否需要强制登录
    forcedLogin: false,
    hasLogin: false,
    userName: '',
    loading: false,
    navbars: [{
      id: 0,
      name: '首页',
      link: '/',
      icon: 'wap-home-o',
      isActive: true
    },
    {
      id: 1,
      name: '全部商品',
      link: '/goods',
      icon: 'gift-o',
      isActive: false
    },
    {
      id: 2,
      name: '新品上市',
      link: '/arrival',
      icon: 'medel-o',
      isActive: false
    },
    {
      id: 3,
      name: '生活常识',
      link: '/lists',
      icon: 'newspaper-o',
      isActive: false
    },
    {
      id: 4,
      name: '公司介绍',
      link: '/about',
      icon: 'notes-o',
      isActive: false
    },
    {
      id: 5,
      name: '问答Q&A',
      link: '/quora',
      icon: 'question-o',
      isActive: false
    },
    {
      id: 6,
      name: '自有品牌',
      link: '/brand',
      icon: 'award-o',
      isActive: false
    }
    ],
    category: [{
      id: 0,
      name: '厨房用品',
      type: 'CFYP',
      ename: 'KITCHEN SUPPLIES',
      bgImg: '/static/images/type/p1.png'
    },
    {
      id: 1,
      name: '家庭清洁',
      type: 'CFYP',
      ename: 'HOME CLEANING',
      bgImg: '/static/images/type/p2.png'
    },
    {
      id: 2,
      name: '个人护理',
      type: 'CFYP',
      ename: 'PERSONAL CARE',
      bgImg: '/static/images/type/p3.png'
    },
    {
      id: 3,
      name: '汽车清洁',
      type: 'CFYP',
      ename: 'AUTOMOBILE CLEANING',
      bgImg: '/static/images/type/p4.png'
    },
    {
      id: 5,
      name: '收纳用品',
      type: 'SNYP',
      ename: 'STORAGE PRODUCTS',
      bgImg: '/static/images/type/p5.png'
    },
    {
      id: 6,
      name: '其他',
      type: 'QT',
      ename: 'OTHERS',
      bgImg: '/static/images/type/p6.png'
    }
    ]
  },
  getters: {
    getCategoryData (state) {
      return state.category
    }
  },
  mutations: {
    setNavActvie (state, pathName) {
      state.navbars.forEach(item => {
        item.isActive = false
        if (item.link === pathName) {
          item.isActive = true
        }
      })
    },
    login (state, userName) {
      state.userName = userName || '新用户'
      state.hasLogin = true
    },
    loginout (state) {
      state.userName = ''
      state.hasLogin = false
      localStorage.removeItem('jwtToken')
    },
    setLoading (state, flag) {
      state.loading = flag
    }
  },
  actions: {
    login (context, userName) {
      context.commit('login', userName)
    },
    loginout (context) {
      context.commit('loginout')
    },
    setNavActvie (context, pathName) {
      context.commit('setNavActvie', pathName)
    },
    setLoading (context, flag) {
      context.commit('setLoading', flag)
    }
  }
})

export default store
