<template>
  <div class="login">
    <TopHeader>
      <template v-slot:title>账户登录</template>
    </TopHeader>
    <div class="login-text" v-show="!hasLogin">
      <van-cell-group>
        <van-field
          v-model="username"
          required
          clearable
          label="用户名"
          right-icon="question-o"
          placeholder="请输入用户名"
          @click-right-icon="$toast('请输入用户名')"
          error
        />
        <van-field
          v-model="password"
          type="password"
          clearable
          label="密码"
          placeholder="请输入密码"
          required
          error
        />
      </van-cell-group>
      <div class="login-info">
        <strong>还没有账号？</strong>
        <router-link to="/register">立即注册</router-link>
      </div>
    </div>
    <div class="login-btn" v-show="!hasLogin">
      <van-button type="danger" @click="login">立即登录</van-button>
    </div>
    <div class="login-out" v-show="hasLogin">
      <van-button type="danger" @click="loginout">用户注销</van-button>
    </div>
  </div>
</template>

<script>
import Vue from 'vue'
import { mapState } from 'vuex'
import jwt_decode from 'jwt-decode'
import TopHeader from '@/views/comm/TopHeader'
import { Field, Button, Cell, CellGroup } from 'vant'
Vue.use(Field)
  .use(Button)
  .use(Cell)
  .use(CellGroup)
export default {
  name: 'Login',
  data () {
    return {
      username: '',
      password: ''
    }
  },
  methods: {
    login () {
      let userName = this.username
      let password = this.password
      // userName = !this.$utils.isEmpty(userName) ? userName : "";
      // password = !this.$utils.isEmpty(password) ? password : "";
      // console.log(userName, password);

      if (this.$utils.isEmpty(userName)) {
        return this.$toast.fail('用户名不能为空！')
      }
      if (this.$utils.isEmpty(password)) {
        return this.$toast.fail('密码不能为空！')
      }
      // 登录请求
      this.$post('/api/Member/Login', { userName, password }).then(res => {
        if (!res.code) {
          const { token, userName } = res.data
          const jwtToken = 'Bearer ' + token
          const decode = jwt_decode(jwtToken)
          console.log(jwtToken)
          console.log(decode)
          localStorage.setItem('jwtToken', jwtToken)
          this.$store.dispatch('login', decode.memberUser)
          this.$toast.success('登录成功！')
          this.$router.push('/home')
        }
      })
    },
    loginout () {
      this.$store.dispatch('loginout')
    }
  },
  components: {
    TopHeader
  },
  computed: {
    ...mapState(['hasLogin'])
  }
}
</script>

<style lang="less" scoped>
@fc8: #fc8481;
@fe6: #fe643d;
.login {
  padding-bottom: 288px;
  .login-text {
    padding: 104px 0 0;
    width: 816px;
    margin: 0 auto;
    .login-info {
      padding: 94px 0 104px;
      margin: 0 auto;
      a {
        color: @fc8;
      }
    }
  }
  .login-btn,
  .login-out {
    width: 816px;
    margin: 0 auto;
    text-align: center;
    button {
      width: 596px;
      background-color: @fe6;
      border: 1px solid @fe6;
    }
  }
  .login-out {
    height: 480px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}
</style>
