import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '@/views/home/Home'
import Goods from '@/views/product/Goods'
import Arrival from '@/views/product/Arrival'
import Brand from '@/views/product/Brand'
import Shop from '@/views/product/Shop'
import Detail from '@/views/news/Detail'
import Lists from '@/views/news/Lists'
import Search from '@/views/search/Search'
import Quora from '@/views/rests/Quora'
import About from '@/views/rests/About'
import Account from '@/views/account/Account'
import Cart from '@/views/account/Cart'
import Order from '@/views/account/Order'
import QA from '@/views/account/QA'
import Address from '@/views/account/Address'
import Login from '@/views/users/Login'
import Register from '@/views/users/Register'

Vue.use(VueRouter)

const routes = [{
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/goods',
    name: 'Goods',
    component: Goods
  },
  {
    path: '/arrival',
    name: 'Arrival',
    component: Arrival
  },
  {
    path: '/shop',
    name: 'Shop',
    component: Shop
  },
  {
    path: '/detail',
    name: 'Detail',
    component: Detail
  },
  {
    path: '/lists',
    name: 'Lists',
    component: Lists
  },
  {
    path: '/search',
    name: 'Search',
    component: Search
  },
  {
    path: '/quora',
    name: 'Quora',
    component: Quora
  },
  {
    path: '/about',
    name: 'About',
    component: About
  },
  {
    path: '/brand',
    name: 'Brand',
    component: Brand
  },
  {
    path: '/account',
    name: 'Account',
    component: Account,
    redirect: '/account/cart',
    children: [{
        path: '/account/cart',
        name: Cart,
        component: Cart
      },
      {
        path: '/account/order',
        name: Order,
        component: Order
      },
      {
        path: '/account/qa',
        name: QA,
        component: QA
      },
      {
        path: '/account/address',
        name: Address,
        component: Address
      },
    ]
  },
  {
    path: '/login',
    name: 'Login',
    component: Login
  },
  {
    path: '/register',
    name: 'Register',
    component: Register
  },
  {
    path: '/temp',
    name: 'Temp',
    component: () => import('../views/temp/Temp.vue')
  },
  {
    path: '/home',
    redirect: '/'
  },
  { // 访问错误 404 页面重定向
    path: '*',
    redirect: '/'
  }
]

const router = new VueRouter({
  // mode: 'history',
  base: process.env.BASE_URL,
  routes
})

// 路由全局守卫
router.beforeEach((to, from, next) => {
  if (to.path === '/account') {
    localStorage.jwtToken ? next() : next('/login')
  } else {
    next()
  }
})

export default router
